@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "HelveticaNeueCyr";
  src: url("../fonts/HelveticaNeue/HelveticaNeueCyr-Medium.eot");
  src: local("HelveticaNeueCyr-Medium"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Medium.woff") format("woff"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "HelveticaNeueCyr";
  src: url("../fonts/HelveticaNeue/HelveticaNeueCyr-Roman.eot");
  src: local("HelveticaNeueCyr-Roman"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Roman.eot?#iefix") format("embedded-opentype"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Roman.woff") format("woff"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Roman.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "HelveticaNeueCyr";
  src: url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.eot");
  src: local("HelveticaNeueCyr-Light"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.eot?#iefix") format("embedded-opentype"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.woff") format("woff"),
    url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "DIN Pro";
  src: url("../fonts/DINPro/DINPro-Medium.eot");
  src: local("DIN Pro Medium") local("DINPro-Medium"),
    url("../fonts/DINPro/DINPro-Medium.eot?#iefix") format("embedded-opentype"),
    url("../fonts/DINPro/DINPro-Medium.woff") format("woff"),
    url("../fonts/DINPro/DINPro-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "DIN Pro";
  src: url("../fonts/DINPro/DINPro-Bold.eot");
  src: local("DIN Pro Bold") local("DINPro-Bold"),
    url("../fonts/DINPro/DINPro-Bold.eot?#iefix") format("embedded-opentype"),
    url("../fonts/DINPro/DINPro-Bold.woff") format("woff"),
    url("../fonts/DINPro/DINPro-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "DIN Pro";
  src: url("../fonts/DINPro/DINPro-Black.eot");
  src: local("DIN Pro Black") local("DINPro-Black"),
    url("../fonts/DINPro/DINPro-Black.eot?#iefix") format("embedded-opentype"),
    url("../fonts/DINPro/DINPro-Black.woff") format("woff"),
    url("../fonts/DINPro/DINPro-Black.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
}
